.nota-contenedor {
    width: 200px;
    height: 200px;
    background-color: #dcd80a;
    margin: 5px 0;
    padding: 8px 15px 8px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid white;
    cursor: pointer;    
  }
  
  /* Colores */
  
  .nota-contenedor{
    background-color: #cdd40b;
  }
  
  /* Texto */
  
  .nota-texto {
    width: 100%;
    height: 100%;
    font-size: 15px;
    display: flex;
    color: rgb(0, 0, 0);
    align-items: center;
    overflow-wrap: anywhere;
  }
  
  /* Icono */
  
  .nota-icono {
    width: 25px;
    height: 25px;
    margin: 5px;
    color:#fff;
    display: flex;
    justify-content: flex-start;
  }
  

  /* Estructura Principal */

.nota-formulario {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;     
  }
  
  /* Campo de Texto */
  
  .nota-input {
    width: 350px;
    font-size: 18px;
    background-color: white;
    padding: 14px 32px 14px 16px;
    border-radius: 4px 0 0 4px;
    border: 2px solid #d9ca25;
    outline: none;
  }
  
  /* Boton Agregar Tarea */
  
  .nota-boton {
    padding: 16px;
    font-size: 18px;
    font-family: Roboto, sans-serif;
    border: none;
    border-radius: 0 4px 4px 0;
    cursor: pointer;
    outline: none;
    background: #d9ca25;
    color: #fff;
    text-transform: capitalize;
  }
 
  
  .notas-lista-contenedor {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 15px;
  }

  h1 {
    color: #000000;
    font-size: 25px;
    font-family: Roboto, sans-serif;
    text-align: center;
    margin: 20px 0;
  }

  .aplicacion-notas {
    width: 1200px;
    
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }

  /* Listas de Tareas */

.tareas-lista-principal {
    width: 1200px;
    min-height: 1000px;
    background-color: #d0d0d5;
    padding: 25px;
    margin: 10px;
    border-radius: 26px;
}